body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.centered {
  text-align: center;
}

.spacetb {
  padding-bottom: 5px;
  padding-top: 5px;
}

.xsmall {
  font-size: x-small;
}
.normalSize {
  font-size: 200%;
}

.green {
  text-decoration: line-through;
  color: green;
}

.overdue {
  color: red;
  text-shadow: crimson 5pt;
  text-decoration: underline;
  font-weight: 900;
}

.hide {
  display: none;
}

.viewrow {
  display: block;
}

.mainList {
  margin-top: 20px;
  margin-bottom: 20px;
  padding-bottom: 10px;
  padding-top: 10px;
  width: 90%;
  margin-left: 5%;
  background-color: white;
  border: grey;
  border-radius: 15px;
  border-color: grey;
  border-width: 2px;
  border-style: solid;
  padding-left: 10px;
  padding-right: 10px;
}

body {
  font-family: Impact, 'Haettenschweiler', 'Arial Narrow Bold', sans-serif;
  color: black;
  margin-top: 20px;
}

.mainstyle {
  background: yellow;
  border-style: solid;
  border-width: 6px;
  border-color: black;
  border-radius: 15px;
  margin-bottom: 20px;
}

.heading {
  color: white;
  -webkit-text-decoration: underline white;
          text-decoration: underline white;
  background: black;
  padding-top: 10px;
  padding-bottom: 10px;
  text-align: center;
  font-size: 20pt;
  margin-left: -15px;
  margin-right: -15px;
}

.todoText {
  margin-top: 6px;
}

.buttona {
  margin-bottom: 5px;
  margin-right: 3px;
  margin-left: 3px;
  border: 2px solid black;
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.buttona:hover {
  background-color: black;
  color: white;
  border: 2px solid white;
}

#todo2 p {
  text-decoration: line-through;
}

.notifications {
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

.todoInput {
  margin-bottom: 10px;
  font-size: 20pt;
  width: 80%;
  margin-left: 10%;
  margin-top: 25px;
  text-align: center;
}

.bottom {
  padding-top: 20px;
  background: black;
  color: white;
  text-align: center;
  font-size: 20pt;
  padding-bottom: 10px;
  padding-top: 10px;
  margin-left: -15px;
  margin-right: -15px;
}

.blue {
  color: blue
}

.black {
  color: black
}

.red {
  color: red
}
.green {
  color: green;
  text-decoration: line-through;
}

.addbutton {
  margin-left: 5px;
  margin-right: 5px;
}

.buttonDiv {
  display: flex;
  justify-content: flex-end;
}

@media only screen and (max-width:767px) {
  .bottom {
    color: red;
    -webkit-text-decoration: underline red;
            text-decoration: underline red;
  }
  .buttonDiv {
    display: flex;
    justify-content: flex-start;
  }
}
